import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { CompaniesService, ImageUploadService } from 'src/app/services';
import { Company } from 'src/app/models/company.model';
import { Options } from 'ng5-slider';
import { Router, ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-create-company',
  templateUrl: './create-company.component.html',
  styleUrls: ['../../../../assets/css/pages/create-page.css']
})
export class CreateCompanyComponent implements OnInit {
  current_user;
  company: Company={};
  cover:any;
  coverExist=false;
  coverImage
  avatar:any;
  avatarExist=false;
  avatarImage
  coverUploaded=false;
  avatarUploaded=false;
  options: Options = {
    floor: 0,
    ceil: 50
  };
  defaultImageBath;
  lookUps;
  //  regex = '[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)';
  @ViewChild('Form') form;
  constructor(
    private companiesService:CompaniesService,
    private imageUploadService: ImageUploadService,
    private router:Router,
    private route:ActivatedRoute,
    private notifier: NotifierService,
    private spinner: NgxSpinnerService,
  ) { }
    change(event){
      console.log("****",event.value)
    }
  ngOnInit() {
    this.current_user = JSON.parse(localStorage.getItem('user'));
    console.log(this.current_user.slug)
    this.company.size_from=1;
    this.company.size_to=5;
    this.getLookUps();
    this.getCompanies();
    this.company.tags=[];
  }

  getCompanies(){
    this.companiesService.get(this.current_user.slug).subscribe(
      (res:any)=>{
      }
    )
  }
  getLookUps(){
    this.companiesService.getLookUps().subscribe(
      (res:any)=>{
        this.lookUps=res;
        this.addLookUpBoolean();
      }
    )
  }
  addLookUpBoolean(){
    for(let i=0;i<this.lookUps.length;i++)
    {
      this.lookUps[i]['visible']=false;
    }
  }
  postCompany(){
    // this.router.navigate(['../company-page/',this.company], {relativeTo: this.route});
    this.spinner.show();
    this.company.website='https://'+this.company.website;
    this.companiesService.post(this.current_user.slug,this.company).subscribe(
      res=>{
            if(this.coverExist)
            {
              this.companiesService.patchCover(res.slug,this.cover)
                .subscribe(
                  (res:any)=>{
                    if(this.avatarExist)
                    {
                      this.coverUploaded=true;
                    }
                    else{
                      this.spinner.hide();
                      this.router.navigate(['../company-page/',res.slug], {relativeTo: this.route});
                    }
                }
              )
            }
            if(this.avatarExist)
            {
              this.companiesService.patchAvatar(res.slug,this.avatar)
                .subscribe(
                  (res:any)=>{
                   if(this.coverUploaded){
                    this.spinner.hide();
                    this.router.navigate(['../company-page/',res.slug], {relativeTo: this.route});
                   }
                   else if(!this.coverExist){
                    this.spinner.hide();
                    this.router.navigate(['../company-page/',res.slug], {relativeTo: this.route});
                   }
                }
              )
            }
        if(!this.avatarExist&&!this.coverExist)
        {
          this.spinner.hide();
          this.router.navigate(['../company-page/',res.slug], {relativeTo: this.route});
        }
      }

    )
  }

  uploadCover(event){
    let selectedImage=<File>event.target.files[0];
    let imagedata= new FormData();
    imagedata.append('file',selectedImage);
    this.cover=imagedata;
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.coverImage = reader.result;

      reader.readAsDataURL(file);
  }

    this.coverExist=true;
  }
  uploadAvatar(event){
    let selectedImage=<File>event.target.files[0];
    let imagedata= new FormData();
    imagedata.append('file',selectedImage);
    this.avatar=imagedata;
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = e => this.avatarImage = reader.result;

      reader.readAsDataURL(file);
  }

    this.avatarExist=true;
  }
  pushTagId(id){
    if(this.company.tags.length==0)
      this.company.tags.push(id);
    else if(!this.company.tags.includes(id))
      this.company.tags.push(id);
  }

  removeTagId(id) {

    this.company.tags=this.company.tags.filter(function(ele){
        return ele != id;
    });
 }
 addRemoveLookUp(lookup){
  if(lookup['visible'])
  {
    this.pushTagId(lookup.id);
  }
  else
    this.removeTagId(lookup.id);
 }
}
