  constructor(
    private route: ActivatedRoute,
    private companiesService: CompaniesService,
    // private notifier: NotifierService,
    private spinner: NgxSpinnerService,
    private jobService: JobsService,
    private router: Router,
    // private googleMaps: GoogleMapsApiService,
    private checkLocalStorage: CheckLocalStorageService
  ) {
  }

  @HostListener('window:scroll', ['$event'])
  scroll() {
    if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
      // you're at the bottom of the page
      if (this.Next && !this.once) {
        this.once = true;
        this.jobService.getNext(this.Next).subscribe(
          (res: any) => {
            this.Next = res.next;
            this.once = false;
            this.jobs = [...this.jobs, ...res.results];
          }
        );
      }
    }

  }
  getMyActivity(saved) {
    this.talktoActivity.loadactivity();
  }

  loadPostsActivity() {
    this.talktoActivity.loadPostsActivity();
  }
  // getMyActivity(saved) {
  //   // this.IsSavedPost = saved;
  //   this.talktoActivity.loadactivity();
  // }

  // loadPostsActivity() {
  //   this.talktoActivity.loadPostsActivity();
  // }
  search() {
    this.searchJobs = [];
    this.company = JSON.parse(localStorage.getItem('company'));
    this.jobService.searchJob(this.searchText, this.company.id).subscribe(
      (res: any) => {
        this.searchJobs = res.results;
        this.showAllJobs = false;
        this.showSearchResult = true;
        if (this.searchJobs.length > 0)
          this.No_search_result = false;
        else
          this.No_search_result = true;
      }, err => {
        this.No_search_result = true;
      }
    );
  }

  onKeyUp(event) {
    if (event.target.value.trim() === '' || event.target.value.trim() === null) {
      this.getJobs();
      this.No_search_result = false;
    }
    else if (event.target.value.length > 2) {
      this.search();
      this.No_search_result = false;
    }
    else {
      this.No_search_result = true;
    }
  }

  getJobs() {
    this.jobService.get(this.comopanySlug).subscribe(
      (res: any) => {
        this.jobs = res.results;
        this.showAllJobs = true;
        this.showSearchResult = false;
        this.Next = res.next;
      }
    );
  }

  ngOnInit() {
    this.spinner.show();
    this.checkLocalStorage.getLocalStorage().subscribe(
      value => {
        if (value) {
          this.is_company = true;
        }
        else {
          this.is_company = false;
        }
      }
    );
    this.company.is_admin = true;
    this.current_user = JSON.parse(localStorage.getItem('user'));
    this.getLookUps();
    this.route.params.subscribe(
      (params) => {
        this.params = params.slug;
        this.getCompanies();
        this.getCompany(params.slug);
        // this.spinner.hide();
      }
    );

    // this.googleMaps.getLocationByCoordsEn(this.longitude,this.latitude).subscribe(
    //   res=>{
    //     console.log("location locationss: ",res);

    //   }
    // )

  }

  getCompanies() {
    this.companiesService.get(this.current_user.slug).subscribe(
      (res: any) => {
        this.companies = res.results;

        let flag = this.companies.filter(item => {
          return item.slug === this.params;
        });

      }, err => {
      }
    );
  }

  getCompany(params) {
    this.spinner.show();
    this.companiesService.getCompany(this.current_user.slug, params).subscribe(
      res => {
        this.pageLoading = false;
        this.company = res;

        if (this.company.website && this.company.website.includes('https://'))
          this.company.website = this.company.website.replace('https://', '');

        this.comopanySlug = res.slug;
        this.switchToCompany();
        for (let i = 0; i < this.company.tags.length; i++) {
          this.company.tags[i]['visible'] = true;
        }
        if (this.company.cover != null)
          this.coverImg = this.company.cover.secure_url;
        if (this.company.avatar != null)
          this.avatarImg = this.company.avatar.derived[1].url;
        this.originalCompany = Object.assign({}, this.company);

        this.getJobs();
        if (localStorage.getItem('switch')) {
          this.is_company = true;
        }
        else if (!localStorage.getItem('switch')) {
          this.is_company = false;
        }
        this.checkLocalStorage.getLocalStorage().subscribe(
          (value: any) => {
            if (value) {
              this.is_company = true;
            }
            else {
              this.is_company = false;
            }
          });
        this.spinner.hide();
      }
    );
  }

  getLookUps() {
    this.companiesService.getLookUps().subscribe(
      (res: any) => {
        this.lookUps = res;
        this.addLookUpBoolean();
      }
    );
  }

  addLookUpBoolean() {
    for (let i = 0; i < this.lookUps.length; i++) {
      this.lookUps[i]['visible'] = false;
    }
  }


  createJob() {
    this.router.navigate(['../../../job/create-job/', this.comopanySlug], { relativeTo: this.route });
  }

  switchToCompany() {
    this.companiesService.switchToCompany(this.comopanySlug).subscribe(
      (res: any) => {
        this.is_company = true;
        localStorage.setItem('switch', res.slug);
        this.loadPostsActivity();
      }
    );
  }
  resetCover() {

  }
  uploadCover(event) {
    let selectedImage = <File>event.target.files[0];
    let imagedata = new FormData();
    imagedata.append('file', selectedImage);
    this.cover = imagedata;
    this.spinner.show();
    this.companiesService.patchCover(this.comopanySlug, this.cover)
      .subscribe(
        (res: any) => {
          if (res['status'] == "progress") {
            this.imgUpload = res;
          }
          else {
            this.coverImg = res.cover.secure_url;
            this.company = res;
            this.imgUpload = { status: '', message: 0, filePath: '' };
            if (this.company.website && this.company.website.includes('https://'))
              this.company.website = this.company.website.replace('https://', '');
            for (let i = 0; i < this.company.tags.length; i++) {
              this.company.tags[i]['visible'] = true;
            }
            //this.spinner.hide();
          }
        }
      );
  }

  uploadAvatar(event) {
    let selectedImage = <File>event.target.files[0];
    let imagedata = new FormData();
    imagedata.append('file', selectedImage);
    this.avatar = imagedata;
    this.spinner.show();
    this.companiesService.patchAvatar(this.comopanySlug, this.avatar)
      .subscribe(
        (res: any) => {
          this.avatarImg = res.avatar.derived[1].url;
          this.company = res;
          if (this.company.website && this.company.website.includes('https://'))
            this.company.website = this.company.website.replace('https://', '');
          for (let i = 0; i < this.company.tags.length; i++) {
            this.company.tags[i]['visible'] = true;
          }
          this.spinner.hide();
        }
      );

  }

  updateProperty(tag?) {

    this.spinner.show();
    if (this.company.website && !this.company.website.includes('https://'))
      this.company.website = 'https://' + this.company.website;
    else
      this.company.website = this.company.website;
    this.companiesService.patchCompany(this.comopanySlug, this.company)
      .subscribe(
        (res: any) => {
          this.company = res;
          if (this.company.website && this.company.website.includes('https://'))
            this.company.website = this.company.website.replace('https://', '');
          for (let i = 0; i < this.company.tags.length; i++) {
            this.company.tags[i]['visible'] = true;
          }
          this.originalCompany = Object.assign({}, this.company);
          this.spinner.hide();
        }
      );
  }

  //   pushTagId(lookup){
