  //     this.pushTagId(lookup);

  //  }

  pushTagId(lookup) {
    let flag = this.company.tags.filter(function (ele) {
      return ele.category.id == lookup.id;
    });

    if (!flag.length) {
      let category = {
        'category': lookup
      };
      this.company.tags.push(category);
    }
    // if(this.company.tags.length==0)
    //   this.company.tags.push(lookup);
    // else if(!this.company.tags.includes(lookup.id))
    //   this.company.tags.push(lookup);
  }

  removeTagId(id) {

    this.company.tags = this.company.tags.filter(function (ele) {
      return ele.category.id != id;
    });
  }

  addRemoveLookUp(lookup) {
    if (lookup['visible']) {
      this.pushTagId(lookup);
    }
    else
      this.removeTagId(lookup.id);
  }

  // ifExist(lookup){
